nav {
  position: absolute;
  width: 100%;
  height: 35px;
  transition: box-shadow 0.5s, background 0.5s;
  z-index: 100;
  /*&.scroll {
    box-shadow: -21.213px 21.213px 30px 0px rgba(158, 158, 158, 0.3);
    background: #fff;
  }*/ }
  nav li {
    padding: 0px;
    font-size: 12px; }
    nav li a {
      color: #fff;
      text-decoration: none;
      font-size: 15px;
      float: right; }
  nav .logo {
    width: 60px;
    height: auto;
    margin: 15px;
    float: left; }
  nav .logo-text {
    float: left;
    padding: 20px 0;
    font-weight: bold;
    font-size: 32px;
    margin-left: -10px; }

