.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 45vmin;
  pointer-events: none;
  margin-top: 60px;
  @media screen and (min-width: 992px) {
    height: 50vmin;
  }
}

@keyframes spin {

  from {
    transform: rotate( 0deg );
  }

  to {
    transform: rotate( -360deg );
  }
}


.App-header {
  background: linear-gradient(to left, #000000, #154c83);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  overflow: hidden;
  width: 100%;
  position: relative;
  height: 100%;

  &:after {
      content: ' ';
      position: absolute;
      width: 400vw;
      height: 400vh;
      top: 50%;
      left: 50%;
      z-index: 1;
      margin-top: -200vh;
      margin-left: -200vw;
      animation: spin 240s linear infinite;
      background-size: 240px;
      backface-visibility: visible;
      background-image: url(data:image/svg+xml;base64,);
  }

}

.background-image {
  &:before {
    content: ' ';
      background: #21878c29;
      border-radius: 50%;
      height: 300px;
      width: 300px;
  }

}


